<script>
import TopMenu from "@/components/top-menu.vue";
import LeftMenu from "@/components/left-menu.vue";
import Aside from "@/components/aside.vue";
import Footer from "@/components/footer.vue";
import CosmeItem from "@/components/Cosmeceuticals/cosmeceuticals-item.vue";
import CategItem from "@/components/categories-item.vue";
import { mapActions, mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "cosme",
  data() {
    return {};
  },
  props: {
    Cosmeceuticals: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    ...mapGetters(["PRODUCT"]),
  },
  methods: {
    ...mapActions(["GET_PRODUCT"]),
  },

  components: {
    TopMenu,
    LeftMenu,
    Aside,
    Footer,
    CosmeItem,
    CategItem,
  },
  created() {
    axios
      .get(`http://31.31.203.119/api/categories/${this.$route.params.path}`, {})
      .then((response) => {
        (this.product = response.data),
          this.$store.dispatch("GET_PRODUCT", this.product);
        this.PRODUCT = JSON.parse(localStorage.getItem("GET_PRODUCT"));
      })
      .catch((error) => {
        console.log(error);
        return error;
      });
  },
  mounted() {
    this.GET_PRODUCT();
